
#actived ul li {width:198px; background:#ff6a83; float:left; margin:15px;
}
#actived ul li .lhead {position:relative; padding:8px 10px; cursor: pointer; color:#ffffff; font-weight: 500
}
#actived ul li .lhead span{position:absolute; right:15px
}
#actived ul li ul {display: none
}
#actived ul li ul li {width:180px; background: #ffffff; padding:6px 8px; margin:0; border:1px solid #707070; border-top:0;
}




